.header {
    min-height: 180px;
    height: 33vh;
    background: url('https://images.unsplash.com/photo-1483058712412-4245e9b90334?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1740&q=80'), linear-gradient(0deg, rgba(0, 0, 0, 1), transparent);
    background-blend-mode: hard-light;
    background-size: cover;
    background-position: center 30%;
    background-repeat: no-repeat;
    position: relative;
}

.header time {
    position: absolute;
    bottom: 10%;
    right: 2.5%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: end;
    font-family: 'Russo One';
    font-size: 3rem;
    text-transform: capitalize;
    text-shadow: 0 1px 2px var(--dark);
    color: var(--white);
    transition: color .3s ease-in-out, text-shadow .3s ease-in-out;
}

.day {
    font-size: 1.5rem;
}

.pulse {
    animation: pulse 1s infinite alternate-reverse;
}

@keyframes pulse {
    to {
        opacity: 0;
    }
}

.dark-theme .header time {
    color: var(--dark);
    text-shadow: 0 1px 2px var(--white);
}